import React, { useEffect, useState, useContext } from 'react';
import PopupWithForm from './PopupWithForm';

// ÐÐ¾Ð½ÑÐµÐºÑÑ
import { CurrentUserContext } from '../../contexts/CurrentUserContext';
import { ValidationFormContext } from '../../contexts/validationFormContext';
import { LogicsAllPopups } from '../../contexts/logicsAllPopups';

function EditProfilePopup({ onUpdateUser, loading }) {
  const [profileValue, setProfileValue] = useState({
    profileName: '',
    profileDoes: ''
  });
  const {profileName, profileDoes} = profileValue;

  const [isValidProfileName, setValidProfileName] = useState(false);
  const [isValidProfileDoes, setValidProfileDoes] = useState(false);

  // ÐÐ¾Ð½ÑÐµÐºÑÑ
  const { about, name } = useContext(CurrentUserContext);
  const validation = useContext(ValidationFormContext);
  const {
    isEditAvatarPopupOpen: isOpen,
    closeAllPopups: onClose
  } = useContext(LogicsAllPopups);

  // ÐÑÐ¾ÑÑÐ¼ Ð¿Ð°ÑÐ°Ð¼ÐµÑÑÐ¾Ð¼ Ð¿ÐµÑÐµÐ´Ð°Ð» Ð¿ÐµÑÐµÐ¼ÐµÐ½Ð½ÑÐµ Ð·Ð°Ð²Ð¸ÑÐ¸Ð¼Ð¾ÑÑÐ¸, ÑÐµ ÑÑÐ¾ Ð¸ÑÐ¿Ð¾Ð»ÑÐ·ÑÑÑÑÑ Ð² useEffect
  useEffect(() => {
    setProfileValue({
      profileName: about,
      profileDoes: name
    });
  }, [about, name]);

  useEffect(() => {
    const isValidProfileName = validation(profileValue.profileName, 2, 40);
    const isValidProfileDoes = validation(profileValue.profileDoes, 2, 200);
    // console.log(isValidProfileName, isValidProfileDoes)

    setValidProfileName(isValidProfileName);
    setValidProfileDoes(isValidProfileDoes);
  }, [profileValue, validation]);

  // ÐÐµÐ¼Ð½Ð¾Ð³Ð¾ ÑÑÐ»Ð¾Ð¶Ð½Ð¸Ð» Ð·Ð°Ð´Ð°ÑÑ, ÑÑÐ¾Ð±Ñ Ð¿Ð¾ÑÑÐµÐ½Ð¸ÑÐ¾Ð²Ð°ÑÑÑÑ, Ð¿Ð¾Ð½ÑÑÑ ÐºÐ°Ðº ÑÐµÐ°Ð»Ð¸Ð·ÑÐµÑÑÑ Ð·Ð°Ð´Ð°ÑÐ° Ð¿Ð¾-Ð´ÑÑÐ³Ð¾Ð¼Ñ
  const handleChange = e => {
    setProfileValue(state => {
      const previousValue = Object.assign({}, state);
      previousValue[e.target.name] = e.target.value;
      return previousValue;
    });
  }

  const handleSabmit = e => {
    e.preventDefault();

    // ÐÐµÑÐµÐ´Ð°ÑÐ¼ Ð·Ð½Ð°ÑÐµÐ½Ð¸Ñ ÑÐ¿ÑÐ°Ð²Ð»ÑÐµÐ¼ÑÑ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½ÑÐ¾Ð² Ð²Ð¾ Ð²Ð½ÐµÑÐ½Ð¸Ð¹ Ð¾Ð±ÑÐ°Ð±Ð¾ÑÑÐ¸Ðº
    onUpdateUser(profileValue);
  };

  return (
    <PopupWithForm title={'Ð ÐµÐ´Ð°ÐºÑÐ¸ÑÐ¾Ð²Ð°ÑÑ Ð¿ÑÐ¾ÑÐ¸Ð»Ñ'} name={'popup_edit_profile'} isOpen={isOpen} onClose={onClose} onSubmit={handleSabmit}>
      <>
        <label className="popup__form-label">
          {/* Ð¡ Ð¿Ð¾Ð¼Ð¾ÑÑÑ value Ð¸ onChange ÑÐ¾Ð·Ð´Ð°Ð» ÑÐ¿ÑÐ°Ð²Ð»ÑÑÑÐ¸Ð¹ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½Ñ, Ð³Ð´Ðµ ÑÐ¾Ð´ÐµÑÐ¶Ð¸Ð¼Ð¾Ðµ Ð±ÐµÑÐµÑÑÑ Ð¸Ð· state ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½ÑÐ° */}
          <input type="text" className="popup__form-input popup__form-input_value_name" id="profile-name-input" placeholder="ÐÐ¼Ñ" name="profileName" minLength="2" maxLength="40" required value={profileName} onChange={handleChange} style={{borderBottom: !isValidProfileName ? '1px solid red' : ''}} />
          <span className={!isValidProfileName ? "popup__error-message profile-name-input-error popup__error-message_active" : "popup__error-message profile-name-input-error"}>ÐÑÐ¸Ð±ÐºÐ° Ð²Ð°Ð»Ð¸Ð´Ð°ÑÐ¸Ð¸</span>
        </label>
        <label className="popup__form-label">
          <input type="text" className="popup__form-input popup__form-input_value_does" id="profile-does-input" placeholder="ÐÐµÑÑÐµÐ»ÑÐ½Ð¾ÑÑÑ" name="profileDoes" minLength="2" maxLength="200" required value={profileDoes} onChange={handleChange} style={{borderBottom: !isValidProfileDoes ? '1px solid red' : ''}} />
          <span className={!isValidProfileDoes ? "popup__error-message profile-does-input-error popup__error-message_active" : "popup__error-message profile-does-input-error"}>ÐÑÐ¸Ð±ÐºÐ° Ð²Ð°Ð»Ð¸Ð´Ð°ÑÐ¸Ð¸</span>
        </label>
        <button className="button-popup button-popup_edit_profile" type="submit" disabled={isValidProfileName && isValidProfileDoes ? false : true} style={{opacity: !(isValidProfileName && isValidProfileDoes) ? '.2' : ''}}>
          {loading ? 'Ð¡Ð¾ÑÑÐ°Ð½Ð¸ÑÑ...' : 'Ð¡Ð¾ÑÑÐ°Ð½Ð¸ÑÑ'}
        </button>
      </>
    </PopupWithForm>
  );
}

export default EditProfilePopup;
